.TodoSearch{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    background: #F9FBFC;
    border-radius: 5px;
    border: 2px solid #202329;
    margin: 40px 0px;
    height: 64px;
    width: 100%;
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder{
    color: #A5A5A5;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
}

.TodoSearch:focus{
    outline-color: #4d77ff9d;
    border: 1px solid #202329;

    

    /* outline-color: #61DAFA ; */
}